import React, { Component } from 'react';
import "./style.css";

class Search extends Component {
    constructor(props){
        super(props)
        this.state = { 
            movies:[],
        } 
    }    
    render() { 
        return ( 
            <div className = "search-container">
                <form className = "search-form" onSubmit = { this.props.submitHandler }>
                    <fieldset className = "search-form-fieldset">
                        <legend> Movie name:</legend>
                        <input 
                            className = "search-form-input" 
                            type = "text" 
                            id = "moviename" 
                            name = "moviename" 
                            value = {this.props.moviename}
                            placeholder = "Enter a Movie Name" 
                            onChange ={this.props.onChangeHandler} 
                        />
                        <button className = "search-form-submit" >
                            <svg xmlns="http://www.w3.org/2000/svg" height="36px" viewBox="0 0 24 24" width="36px" fill="#fff">
                                <path d="M0 0h24v24H0V0z" fill="none"/>
                                <path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"/>
                            </svg>
                        </button>
                    </fieldset>
                </form> 
                <div className = "search-error-message" style = {{background:this.props.messageStyle}}>{this.props.errorMessage}</div>
            </div>
        );
    }
}

export default Search;
